import React from "react";
import Form from "./Form";

class Item extends React.Component {
	state = {
		isFormShow: false
	};
	renderForm = () => {
		this.setState({
			isFormShow: true
		});
	};

	closeFrom = () => {
		this.setState({
			isFormShow: false
		});
	};
	renderIcon = type => {
		return (
			<div className="icon-box">
				<i className={`fa fa-${type}`} id={type} />
				{type === "calendar" ? (
					<span>{this.props.item.deadline.format("l")}</span>
				) : null}
			</div>
		);
	};

	render() {
		const {
			title,
			deadline,
			isImportant,
			isFinish,
			comment,
			file,
			id
		} = this.props.item;
		const finishStyle = isFinish
			? {
					textDecoration: "line-through",
					opacity: "0.5",
					fontStyle: "italic"
			  }
			: {};
		return (
			<React.Fragment>
				{this.state.isFormShow ? (
					<Form
						updateItem={this.props.updateItem}
						closeForm={this.closeFrom}
						isEditMode={true}
						item={this.props.item}
					/>
				) : (
					<div
						className="item"
						style={isImportant ? { backgroundColor: "#fff2dc" } : {}}
					>
						<div className="checkbox">
							<input
								type="checkbox"
								name={id}
								id={id}
								checked={isFinish}
								readOnly
								onClick={() => this.props.handleItemState(id, "isFinish")}
							/>
							<label htmlFor={id} />
						</div>
						<div className="content">
							<span className="title" style={finishStyle}>
								{title}
							</span>
							<div className="icons">
								{file ? this.renderIcon("file") : null}
								{comment ? this.renderIcon("comment") : null}
								{deadline ? this.renderIcon("calendar") : null}
							</div>
						</div>
						<div className="icon-box">
							<i
								className="fa fa-star"
								id="star"
								onClick={() => this.props.handleItemState(id, "isImportant")}
								style={isImportant ? { color: "#f5a623" } : {}}
							/>
						</div>
						<div className="icon-box">
							<i
								className="fa fa-pencil"
								id="pencil"
								onClick={this.renderForm}
							/>
						</div>
					</div>
				)}
			</React.Fragment>
		);
	}
}

export default Item;



