import React, { Component } from "react";
import "./App.css";
import Header from "./components/Header";
import List from "./components/List";

class App extends Component {
	state = {
		mode: "My Tasks",
		modeList: ["My Tasks", "In Progress", "Completed"],
		items: []
	};

	addItem = item => {
		const items = [...this.state.items];
		items.push(item);
		this.setState({ items });
	};

	updateItem = (id, updatedItem) => {
		const items = this.state.items.map(item => {
			if (item.id === id) return updatedItem;
			return item;
		});
		this.setState({ items });
	};

	handleItemState = (id, type) => {
		const items = this.state.items.map(item => {
			if (item.id === id) {
				item[type] = !item[type];
			}
			return item;
		});
		this.setState({ items });
	};

	changeMode = mode => {
		this.setState({ mode });
	};

	render() {
		return (
			<div className="App">
				<Header modeList={this.state.modeList} changeMode={this.changeMode} />
				<List
					mode={this.state.mode}
					addItem={this.addItem}
					updateItem={this.updateItem}
					handleItemState={this.handleItemState}
					items={this.state.items}
				/>
			</div>
		);
	}
}

export default App;



