import React from "react";

class Header extends React.Component {
	render() {
		return (
			<header>
				<ul>
					{this.props.modeList.map((mode, index) => {
						return (
							<li key={index} onClick={() => this.props.changeMode(mode)}>
								{mode}
							</li>
						);
					})}
				</ul>
			</header>
		);
	}
}

export default Header;



