import React from "react";
import DatePicker from "react-datepicker";
import moment from "moment";
import "react-datepicker/dist/react-datepicker.css";

class Form extends React.Component {
	state = {
		title: "",
		deadline: moment(),
		comment: "",
		isImportant: false,
		isFinish: false
	};

	editDeadline = date => {
		this.setState({
			deadline: date
		});
	};

	editItem = e => {
		const { name, value } = e.currentTarget;
		this.setState({ [name]: value });
	};

	handleItemState = type => {
		this.setState({
			[type]: !this.state[type]
		});
	};

	addItem = e => {
		e.preventDefault();
		let { title, deadline, comment, isImportant, isFinish } = this.state;
		if (!title) {
			title = "Type Something Here...";
		}
		const item = {
			id: Date.now(),
			title,
			deadline,
			comment,
			isImportant,
			isFinish
		};
		this.props.addItem(item);
		this.clearState();
		this.props.closeForm();
	};

	updateItem = e => {
		e.preventDefault();
		let { title, deadline, comment, isImportant, isFinish } = this.state;
		const { id } = this.props.item;
		if (!title) {
			title = "Type Something Here...";
		}
		const updatedItem = {
			id,
			title,
			deadline,
			comment,
			isImportant,
			isFinish
		};
		this.props.updateItem(id, updatedItem);
		this.clearState();
		this.props.closeForm();
	};

	clearState = () => {
		this.setState({
			title: "",
			deadline: moment(),
			comment: "",
			isImportant: false
		});
	};

	componentDidMount() {
		if (!this.props.isEditMode) return;
		const { title, deadline, comment, isImportant, isFinish } = this.props.item;
		this.setState({ title, deadline, comment, isImportant, isFinish });
	}

	render() {
		const { title, deadline, comment, isImportant, isFinish } = this.state;
		const { isEditMode } = this.props;
		const importantStyle = isImportant ? { color: "#f5a623" } : {};
		const finishStyle = isFinish
			? {
					textDecoration: "line-through",
					opacity: "0.5",
					fontStyle: "italic"
			  }
			: {};
		return (
			<form
				className="form"
				onSubmit={isEditMode ? this.updateItem : this.addItem}
			>
				<div className="top">
					<div className="checkbox">
						<input
							type="checkbox"
							name="newItem"
							id="newItem"
							onClick={() => this.handleItemState("isFinish")}
						/>
						<label htmlFor="newItem" />
					</div>
					<input
						type="text"
						name="title"
						onChange={this.editItem}
						value={title}
						className="input__text"
						style={finishStyle}
						placeholder="Type Something Here..."
					/>
					<div className="icon-box">
						<i
							className="fa fa-star"
							id="star"
							onClick={() => this.handleItemState("isImportant")}
							style={importantStyle}
						/>
					</div>
					<div className="icon-box">
						<i className="fa fa-pencil" id="pencil" />
					</div>
				</div>
				<div className="body">
					<div className="datepicker">
						<div className="icon-box">
							<i className="fa fa-calendar" id="calendar" />
							<h4>Deadline</h4>
						</div>
						<DatePicker
							className="input__body"
							name="deadline"
							selected={deadline}
							onChange={this.editDeadline}
						/>
					</div>
					<div className="file">
						<div className="icon-box">
							<i className="fa fa-file" id="file" />
							<h4>File</h4>
						</div>
						<div className="box__add--file input__body">
							<span></span>
						</div>
					</div>
					<div className="comment">
						<div className="icon-box">
							<i className="fa fa-comment" id="comment" />
							<h4>Comment</h4>
						</div>
						<textarea
							className="input__body"
							cols="70"
							rows="5"
							onChange={this.editItem}
							name="comment"
							value={comment}
						/>
					</div>
				</div>
				<div className="footer">
					<button
						type="reset"
						className="button button__cancel"
						onClick={this.props.closeForm}
					>
						<span>X Cancel</span>
					</button>
					<button className="button button__add" type="submit">
						<span>{isEditMode ? "Save" : "+ Add Task"}</span>
					</button>
				</div>
			</form>
		);
	}
}

export default Form;



