import React from "react";
import Form from "./Form";
import Item from "./Item";

class List extends React.Component {
	state = {
		isFormShow: false
	};
	renderForm = () => {
		this.setState({
			isFormShow: true
		});
	};

	closeFrom = () => {
		this.setState({
			isFormShow: false
		});
	};

	renderItems = () => {
		const { mode } = this.props;
		let items = [...this.props.items];
		if (mode === "In Progress") {
			items = items.filter(item => !item.isFinish);
		} else if (mode === "Completed") {
			items = items.filter(item => item.isFinish);
		}
		return items.map(item => {
			return (
				<Item
					handleItemState={this.props.handleItemState}
					updateItem={this.props.updateItem}
					item={item}
					key={item.id}
				/>
			);
		});
	};

	render() {
		return (
			<div className="list">
				<div className="button__add--list" onClick={this.renderForm}>
					<div className="title">+ Add Task</div>
				</div>
				{this.state.isFormShow ? (
					<Form
						addItem={this.props.addItem}
						closeForm={this.closeFrom}
						isEditMode={false}
					/>
				) : null}
				{this.renderItems()}
			</div>
		);
	}
}

export default List;



